import VisualComponent from "jx/core/comps/VisualComponent";
import Common from "jx/editor/editionitems/Common";
let GROUP = `
<div class="tab-pane" id="ID">
</div>
`;
export default class ScreenData extends VisualComponent {
    constructor(dataObject) {
        super(dataObject);
        this.controller = this.dataObject.controller;
        this.commonLib = new Common({ jx: this.jx });
        var item = $(GROUP)[0];
        item.id = "Screen: " + this.data.id;
        $(item).append(`<h4>${item.id}</h4>`);
        this.item = item;
        this.view = this.item;
    }
}
